import * as types from './types.js'

// actionscommit

// 
// vuex
// store.commit()
export default {
  UserLogin ({ commit }, data) {
    commit(types.LOGIN, data)
  },
  UserLogout ({ commit }) {
    commit(types.LOGOUT)
  },
  userName ({ commit }, data) {
    commit(types.userName, data)
  },
  userType ({ commit }, data) {
    commit(types.userType, data)
  }
}

