import * as types from './types.js'

// token
// 1.vuexvuextoken
// 2.window.localStorage: localStoragelocalStoragetoken
// state.token,vue-routerdemo
// 3.window.localStorage: localStoragelocalStoragetoken,
// tokenlocalStorage

const mutations = {
  [types.LOGIN]: (state, data) => {
    // token
    state.token = data
    window.localStorage.setItem('token', data)
  },
  [types.LOGOUT]: (state) => {
    // token
    state.token = null
    window.localStorage.removeItem('token')
  },
  [types.userName]: (state, data) => {
    // 
    state.userName = data
    window.localStorage.setItem('userName', data)
  },
  [types.userType]: (state, data) => {
    // 
    state.userType = data
    window.localStorage.setItem('userType', data)
  }
}
let stateKeys = [types.BREADLIST]
stateKeys.map(item => {
  mutations[item] = (state, payload) => { state[item] = payload }
})

export default mutations
